import { Fragment } from "react";
import {
  json,
  useLoaderData,
  useParams,
  useRouteLoaderData,
} from "react-router-dom";
import ProductDetail from "../components/Products/ProductDetail";
import axios from "axios";

function DetailPage() {
  const data = useLoaderData();

  return (
    <ProductDetail products={data.productCategory} product={data.product} />
  );
}

export default DetailPage;

export async function loader({ params, request }) {
  console.log(params.productId);
  // sử dụng hàm bất đồng bộ để fetch data từ firebase
  const response = await fetch(
    `https://boutiquebe-60a72a49dd20.herokuapp.com/product/${params.productId}`
  );

  // nếu lỗi thì trả về messesge không thể lấy dữ liệu sản phẩm
  if (!response.ok) {
    throw json({ message: "Could not fetch products data" }, { status: 500 });
  } else {
    //còn không thì chuyển đổi qua json
    // và trả về kết quả
    const resData = await response.json();
    console.log(resData);
    return resData;
  }
}
