import { json, redirect } from "react-router-dom";
import Login from "../components/Auth/Login";
import { getUsers, setUser, setUserLogin } from "../components/Until/auth";
import { authActions } from "../store/auth";
import store from "../store";

function LoginPage() {
  return <Login />;
}

export default LoginPage;

export async function action({ request }) {
  // láy dữ liệu từ form trả về
  const formData = await request.formData();
  // lấy dữ liệu UserArr từ localstorage
  // đảy dữ liệu vào 1 object
  const userData = {
    email: formData.get("email"),
    password: formData.get("password"),
  };
  console.log(userData);

  const response = await fetch(
    "https://boutiquebe-60a72a49dd20.herokuapp.com/auth/signin",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(userData),
    }
  );
  const result = await response.json();
  console.log(result);

  if (result.validationErrors) {
    return json(result);
  }

  try {
    store.dispatch(
      authActions.ON_LOGIN({
        isAuth: true,
        ...result,
      })
    );
  } catch (error) {
    console.log(error);
  }
  // đăng nhập thành công chuyển sang chang home
  return redirect("/");
}
