import { Fragment } from "react";

import CategoryLists from "../components/CategoryLists/CategoryLists";
import Banner from "../components/Banner/Banner";
import ProductsList from "../components/Products/ProductsList";
import { json } from "react-router-dom";
import StoreInformation from "../components/StoreInfo/StoreInfor";
import Subcribe from "../components/StoreInfo/Subcripbe";
import Popup from "../components/Popup";

function HomePage() {
  return (
    <Fragment>
      <Banner />
      <CategoryLists />
      <ProductsList />
      <StoreInformation />
      <Subcribe />
      <Popup />
    </Fragment>
  );
}

export default HomePage;

export async function loader() {
  // sử dụng hàm bất đồng bộ để fetch data từ firebase
  const response = await fetch(
    "https://boutiquebe-60a72a49dd20.herokuapp.com/product"
  );
  // nếu lỗi thì trả về messesge không thể lấy dữ liệu sản phẩm
  if (!response.ok) {
    throw json({ message: "Could not fetch products data" }, { status: 500 });
  } else {
    //còn không thì chuyển đổi qua json
    // và trả về kết quả
    const resData = await response.json();
    console.log(resData);
    return resData;
  }
}
