import { isRouteErrorResponse, json, redirect } from "react-router-dom";
import Register from "../components/Auth/Register";
import { getUsers, setUser } from "../components/Until/auth";

function RegisterPage() {
  return <Register />;
}

export default RegisterPage;

export async function action({ request }) {
  //lây dữ liệu từ form router
  const formData = await request.formData();
  const userData = {
    fullName: formData.get("fullName"),
    email: formData.get("email").toLowerCase(),
    password: formData.get("password"),
    phone: formData.get("phone"),
  };
  console.log(userData);
  // đẩy dữ liệu lên server
  const response = await fetch(
    "https://boutiquebe-60a72a49dd20.herokuapp.com//auth/signup",
    {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(userData),
    }
  );
  const result = await response.json();
  console.log(result);

  // Nếu có lỗi Validation thì trả về lỗi
  if (result.validationErrors) {
    return new json(result);
  }
  // và chuyển sang trang login

  return redirect("/login");
}
