import { RouterProvider, createBrowserRouter } from "react-router-dom";
import MainLayout from "./components/Layouts/Main";
import HomePage, { loader as productsLoader } from "./pages/Home";
import ShopPage from "./pages/Shop";
import DetailPage, { loader as detailLoader } from "./pages/Detail";
import CartPage from "./pages/Cart";
import CheckoutPage, { action as checkoutLoader } from "./pages/Checkout";
import LoginPage, { action as LoginAction } from "./pages/Login";
import RegisterPage, { action as RegisterAction } from "./pages/Register";
import ErrorPage from "./pages/Error";
import { useEffect } from "react";
import { getCart, setCart } from "./components/Until/cart";
import { useDispatch, useSelector } from "react-redux";
import { cartActions } from "./store/cart";
import { authActions } from "./store/auth";
import { logoutAction } from "./components/logout";
import HistoryPage, { loader as historyLoader } from "./pages/History";
import HistoryDetailPage, {
  loader as historyDetailLoader,
} from "./pages/HistoryDetail";

const router = createBrowserRouter([
  {
    path: "/",
    element: <MainLayout />,
    errorElement: <ErrorPage />,
    children: [
      {
        index: true,
        element: <HomePage />,
        loader: productsLoader,
      },
      {
        path: "shop",
        element: <ShopPage />,
        loader: productsLoader,
      },
      {
        path: "detail/:productId",
        element: <DetailPage />,
        loader: detailLoader,
      },
      {
        path: "cart",
        element: <CartPage />,
      },
      {
        path: "checkout",
        element: <CheckoutPage />,
        action: checkoutLoader,
      },
      {
        path: "history",
        element: <HistoryPage />,
        loader: historyLoader,
      },
      {
        path: "history/:orderId",
        element: <HistoryDetailPage />,
        loader: historyDetailLoader,
      },
      {
        path: "login",
        element: <LoginPage />,
        action: LoginAction,
      },
      {
        path: "register",
        element: <RegisterPage />,
        action: RegisterAction,
      },
      {
        path: "/logout",
        action: logoutAction,
      },
    ],
  },
]);

function App() {
  const dispatch = useDispatch();
  const cartState = useSelector((state) => state.cart);
  let isInitial = true;
  const cartData = getCart();

  // const expiryDate = localStorage.getItem("expiryDate");
  // useEffect này sẽ chaỵ lần đàu tiên khi khở động ứng dụng
  // là get dữ liệu từ localstore vào redux
  useEffect(() => {
    dispatch(cartActions.LOAD_CART(cartData));
    // kiểm tra thử xem user có đang login hay k
    const token = localStorage.getItem("token");
    const userId = localStorage.getItem("userId");
    const email = localStorage.getItem("email");
    if (token) {
      dispatch(
        authActions.ON_LOGIN({
          isAuth: true,
          token: token,
          userId: userId,
          email: email,
        })
      );
    }
  }, []);

  // useEffect sẽ chạy khi người dàng mửo app
  // nếu cartState có sự thay đổi thì lưu vào cảt reducer
  useEffect(() => {
    if (isInitial) {
      setCart(cartState);
      isInitial = false;
      return;
    }
  }, [cartState]);
  return <RouterProvider router={router} />;
}

export default App;
