import { Fragment } from "react";
import { Link, useLoaderData } from "react-router-dom";

function HistoryDetail() {
  const data = useLoaderData();
  console.log(data);
  // lấy tổng tiền và đổi lại theo format tiền vnd
  // totalPrice là số nên sử dụng toString để chuyển về kiểu dữ liệu string
  const totalPrice = data.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

  return (
    <Fragment>
      <div className=" p-5 justify-content-between align-items-center">
        <div className=" py-5 px-4 ">
          <h5 className="fs-1">INFORMATION ORDER </h5>
          <p className="text-secondary">ID User: {data.userId}</p>
          <p className="text-secondary">Full Name: {data.fullName}</p>
          <p className="text-secondary">Phone: {data.phone}</p>
          <p className="text-secondary">Address: {data.address}</p>
          <p className="text-secondary">Total: {totalPrice} VND</p>
        </div>
      </div>
      <div className="g-2 mb-5 py-2">
        <div className="w-100">
          <table className="w-100">
            <tbody>
              <tr className="bg-light ">
                <th className="text-center py-3  ">ID PRODUCT</th>
                <th className="text-center py-3 ">IMAGE</th>
                <th className=" text-center py-3 ">NAME</th>
                <th className=" text-center py-3 ">PRICE</th>
                <th className=" text-center py-3 ">COUNT</th>
              </tr>
              {data.cart.items.map((order) => {
                const price = order.productId.price
                  .toString()
                  .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
                return (
                  <tr>
                    <td className="text-center py-3">{order.productId._id}</td>
                    <td className="text-center py-3">
                      <img
                        width={200}
                        height={200}
                        src={order.productId.img1}
                      />
                    </td>
                    <td className="text-center py-3">{order.productId.name}</td>
                    <td className="text-center py-3">{price} VND</td>
                    <td className="text-center py-3">{order.quantity}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </Fragment>
  );
}

export default HistoryDetail;
