import { Fragment } from "react";
import { useDispatch } from "react-redux";
import { cartActions } from "../../store/cart";

function CartItem(props) {
  // sử dụng useDispatch để có thể chạy hàm action trong redux
  const dispatch = useDispatch();
  // lấy tổng tiền và đổi lại theo format tiền vnd
  // totalPrice là số nên sử dụng toString để chuyển về kiểu dữ liệu string
  const totalPrice = props.product.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  const price = props.product?.price
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  // function xóa lượng hàng trong giỏ
  const removeProductFromCart = () => {
    dispatch(cartActions.DELETE_CART(props.product.id));
  };
  // function tăng số lượng hàng trong giỏ
  const addProductToCart = () => {
    dispatch(
      cartActions.ADD_CART({
        id: props.product.id,
        price: Number(props.product.price),
        name: props.product.name,
        img: props.product.img,
        quantity: 1,
      })
    );
  };
  // function tăng số lượng hàng trong giỏ
  const updateProductHandler = () => {
    dispatch(cartActions.UPDATE_CART(props.product.id));
  };
  return (
    <Fragment>
      <tr>
        <td className="text-center py-3 ">
          <img style={{ width: "120px" }} src={props.product.img}></img>
        </td>
        <td className="text-center py-3 w-25 fw-bold">{props.product.name}</td>
        <td className=" text-center py-3 ">{price} VND</td>
        <td className=" text-center py-3 fs-5">
          <i
            onClick={updateProductHandler}
            className="fa-solid fa-caret-left me-2 pe-1 fs-5"
          />
          {props.product.quantity}
          <i
            onClick={addProductToCart}
            className="fa-solid fa-caret-right ms-2 ps-1 fs-5"
          />
        </td>
        <td className=" text-center py-3 ">{totalPrice} VND</td>
        <td className=" text-center py-3 ">
          <i
            onClick={removeProductFromCart}
            className="fa-regular fa-trash-can"
          ></i>
        </td>
      </tr>
    </Fragment>
  );
}

export default CartItem;
