import { json, redirect } from "react-router-dom";
import Checkout from "../components/Cart/Checkout";
import { getUser } from "../components/Until/auth";

function CheckoutPage() {
  return <Checkout />;
}

export default CheckoutPage;
export async function action({ request }) {
  // láy dữ liệu từ form trả về
  const formData = await request.formData();
  const userId = localStorage.getItem("userId");
  console.log(userId);
  // đảy dữ liệu vào 1 object
  const orderData = {
    email: formData.get("email"),
    fullName: formData.get("fullName"),
    phone: formData.get("phone"),
    cart: JSON.parse(formData.get("cart")),
    address: formData.get("address"),
    userId: userId,
  };
  console.log(orderData);
  const response = await fetch(
    "https://boutiquebe-60a72a49dd20.herokuapp.com/order",
    {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(orderData),
    }
  );
  const result = await response.json();
  console.log(result);

  if (result.validationErrors) {
    return json(result);
  }
  // đăng nhập thành công chuyển sang chang history
  return redirect("/");
}
