function CheckoutItem(props) {
  // chuyển đổi về format tiền tệ VND
  // chuyển từ number sang string
  const totalPrice = props.product.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  return (
    <div className="d-flex align-items-center justify-content-between border-bottom mt-4 pb-1">
      <p className="m-0">{props.product.name}</p>
      <p className="text-secondary m-0">
        {totalPrice} VND x {props.product.quantity}
      </p>
    </div>
  );
}

export default CheckoutItem;
