import { Form, Link, NavLink } from "react-router-dom";
import classes from "./Navbar.module.css";
import { useSelector } from "react-redux";

function NavBarLayout() {
  // useSelector để lấy state trong redux
  // và xác nhận trạng thái hiện có đăng nhập hay k
  const authState = useSelector((state) => state.auth);
  console.log(authState.isAuth);
  return (
    <header className={classes.header}>
      <nav>
        <ul>
          <li>
            <NavLink
              className={({ isActive }) =>
                isActive ? classes.active : undefined
              }
              end
              to={"/"}
            >
              Home
            </NavLink>
          </li>
          <li>
            <NavLink
              className={({ isActive }) =>
                isActive ? classes.active : undefined
              }
              to={"/shop?category=all"}
            >
              Shop
            </NavLink>
          </li>
        </ul>
      </nav>
      <h2>
        <Link to={"/"}>BOUTIQUE</Link>
      </h2>
      <nav>
        <ul>
          {authState.isAuth && (
            <li>
              <NavLink
                to={"/cart"}
                className={({ isActive }) =>
                  isActive ? classes.active : undefined
                }
              >
                <i className={`${classes.icon} fa-solid fa-cart-flatbed`}></i>
                Cart
              </NavLink>
            </li>
          )}

          <li>
            {!authState.isAuth ? (
              <Link to={"/login"}>Login</Link>
            ) : (
              <div>
                <i className={`${classes.icon} fa-solid fa-user`}></i>
                {authState.email}
                <i
                  className={`${classes["icon-down"]} fa-solid fa-caret-down`}
                ></i>
              </div>
            )}
          </li>
          <li>
            {authState.isAuth && (
              <Form method="POST" action={"/logout"}>
                <button className="border-0 bg-white fw-bold">
                  ( Logout )
                </button>
              </Form>
            )}
          </li>
        </ul>
      </nav>
    </header>
  );
}
export default NavBarLayout;
