import { createSlice } from "@reduxjs/toolkit";

export const authSlice = createSlice({
  name: "auth",
  initialState: {
    // state trạng thái login
    isAuth: false,
    // state lưu dữ liệu user đang đăng nhập
    userId: null,
    token: null,
    email: null,
  },
  reducers: {
    ON_LOGIN(state, action) {
      // lấy dữ liêụ được trả về khi đăng nhập thành công
      const userLogin = action.payload;
      // nếu userLogin được trả về thì set isLogin thành true
      // và set UserLogin
      if (userLogin) {
        state.isAuth = true;
        state.userId = userLogin.userId;
        state.token = userLogin.token;
        state.email = userLogin.email;
        localStorage.setItem("token", userLogin.token);
        localStorage.setItem("userId", userLogin.userId);
        localStorage.setItem("email", userLogin.email);
      } else {
        // còn không thì isLogin trả về false
        // và userLogin trả về null
        state.isLogin = false;
        state.userId = null;
        state.token = null;
      }
      console.log(state.userLogin);
    },
    // khi click vào logout
    // set isLogin là false
    // userLogin là null
    ON_LOGOUT(state, action) {
      state.isAuth = false;
      state.userId = null;
      state.token = null;
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      localStorage.removeItem("email");
    },
  },
});

export const authActions = authSlice.actions;
