import { Fragment, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import CheckoutItem from "./CheckoutItem";
import { Form, useActionData } from "react-router-dom";

function Checkout() {
  const data = useActionData();
  const cartState = useSelector((state) => state.cart);

  console.log(cartState);

  // chuyển đổi tổng tiền sang string
  // và chuyển đổi theo format tiền tệ VND
  const totalPrice = cartState.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  return (
    <Fragment>
      <div className=" bg-body-tertiary d-flex p-5 justify-content-between align-items-center">
        <div className=" py-5 px-4 ">
          <h5 className="fs-1">CHECKOUT </h5>
        </div>
        <div className=" py-5 px-4">
          <p>
            HOME / CART / <span className="text-secondary">CHECKOUT</span>
          </p>
        </div>
      </div>
      <h4 className="mt-5 py-4">BILLING DETAILS </h4>
      <div className="row g-2 mb-5">
        <div className="col-7 pe-4 justify-self-center ">
          <Form method="post">
            <div className="w-100 d-flex flex-column ">
              <label className="mb-2">FULL NAME :</label>
              <input
                className="border border-secondary-subtle px-3 py-2 fw-bold"
                placeholder="Enter Your Full Name Here!"
                name="fullName"
              ></input>
              <label className="mt-4 mb-2">EMAIL :</label>
              <input
                className="border border-secondary-subtle px-3 py-2 fw-bold"
                placeholder="Enter Your Email Here!"
                name="email"
              ></input>
              <label className="mt-4 mb-2">PHONE NUMBER :</label>
              <input
                className="border border-secondary-subtle px-3 py-2 fw-bold"
                placeholder="Enter Your Phone Number Here!"
                name="phone"
              ></input>
              <label className="mt-4 mb-2">ADDRESS :</label>
              <input
                className="border border-secondary-subtle px-3 py-2 fw-bold"
                placeholder="Enter Your Address Here!"
                name="address"
              ></input>
              <input
                type="hidden"
                name="cart"
                value={JSON.stringify(cartState)}
              />
              {data && <p className="m-0 mt-3 text-danger">{data.message}</p>}
              <button className="w-25 my-4 border-0 bg-dark text-white fw-bold py-2">
                Place order
              </button>
            </div>
          </Form>
        </div>
        <div className="col-5 p-3 bg-light px-4 py-5 align-self-center">
          <h4>YOUR ORDER</h4>
          {cartState.items.map((product) => (
            <CheckoutItem product={product}></CheckoutItem>
          ))}
          <div className="d-flex align-items-center justify-content-between border-top pt-2">
            <h6 className="m-0 ">TOTAL </h6>
            <p className="m-0  fs-5">{totalPrice} VND </p>
          </div>
        </div>
      </div>
    </Fragment>
  );
}
export default Checkout;
