import { Fragment } from "react";
import { useSelector } from "react-redux";
import CartItem from "./CartItem";
import { Link } from "react-router-dom";

function Cart() {
  // lấy dữ liệu từ redux
  const cartState = useSelector((state) => state.cart);
  // lấy tổng tiền và đổi lại theo format tiền vnd
  // totalPrice là số nên sử dụng toString để chuyển về kiểu dữ liệu string
  const totalPrice = cartState?.totalPrice
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  console.log(cartState);
  return (
    <Fragment>
      <div className=" bg-body-tertiary d-flex p-5 justify-content-between align-items-center">
        <div className=" py-5 px-4 ">
          <h5 className="fs-1">CART </h5>
        </div>
        <div className=" py-5 px-4">
          <p className="text-secondary">CART</p>
        </div>
      </div>
      <h3 className="mt-5 py-4">SHOPPING CART </h3>
      <div className="row g-2 mb-5">
        <div className="col-9 p-2 justify-self-center">
          <div className="w-100 pe-3">
            {cartState.items?.length === 0 ? (
              <p>No Product In Cart</p>
            ) : (
              <table className="w-100">
                <tbody>
                  <tr className="bg-light ">
                    <th className="text-center py-3  ">IMAGE</th>
                    <th className="text-center py-3 ">PRODUCT</th>
                    <th className=" text-center py-3 ">PRICE</th>
                    <th className=" text-center py-3 ">QUANTITY</th>
                    <th className=" text-center py-3 ">TOTAL</th>
                    <th className=" text-center py-3 ">REMOVE</th>
                  </tr>
                  {cartState.items?.map((product) => (
                    <CartItem key={product.id} product={product} />
                  ))}
                </tbody>
              </table>
            )}

            <div className="bg-light d-flex justify-content-between align-items-center">
              <Link
                to={"/shop?category=all"}
                className="text-decoration-none text-black px-3 py-2 mx-4 my-3"
              >
                <i className="fa-solid fa-chevron-left me-2" />
                Continue shopping
              </Link>
              {cartState.items && (
                <Link
                  to={"/checkout"}
                  className="text-decoration-none text-black border border-2 border-dark px-3 py-2 mx-4 my-3"
                >
                  Proceed to checkout
                  <i className="fa-solid fa-chevron-right ms-2"></i>
                </Link>
              )}
            </div>
          </div>
        </div>
        <div className="col-3 p-3 bg-light px-4 py-5 align-self-center">
          <h4>CART TOTAL</h4>
          <div className="d-flex align-items-center justify-content-between border-bottom mt-4 pb-1">
            <h6 className="m-0">SUBTOTAL </h6>
            <p className="text-secondary m-0">{totalPrice} VND</p>
          </div>
          <div className="d-flex align-items-center justify-content-between border-top pt-2">
            <h6 className="m-0 ">TOTAL </h6>
            <p className="m-0 fs-5">{totalPrice} VND </p>
          </div>
          <input
            className=" w-100 border border-light-subtle px-3 py-2 mt-4"
            placeholder="Enter your coupon "
          ></input>
          <button className="w-100 bg-dark border-0 text-light py-2 fw-bold">
            <i className="fa-solid fa-gift me-2" />
            Apply coupon
          </button>
        </div>
      </div>
    </Fragment>
  );
}

export default Cart;
