import { createSlice } from "@reduxjs/toolkit";

const popupSlice = createSlice({
  name: "popup",
  // SHOW_POPUP để lưu dữ liệu khi người dùng click vào sản phẩm
  // HIDE_POPUP đẻ hiển thị trạng thái popup đang được hiển thị hay không
  initialState: {
    SHOW_POPUP: [],
    HIDE_POPUP: false,
  },
  reducers: {
    // lấy dữ liệu được trả về và set vào SHOW_POPUP
    // và hiển thị popup
    showPopup(state, action) {
      const productDetail = action.payload;
      state.SHOW_POPUP = productDetail;
      state.HIDE_POPUP = true;
    },
    // hide pop up để tắt popup
    hidePopup(state, action) {
      state.HIDE_POPUP = false;
    },
  },
});
// export action đẻ có thể thao tác với redux
export const popupActions = popupSlice.actions;

export default popupSlice;
