import { json } from "react-router-dom";
import HistoryDetail from "../components/Cart/HistoryDetail";

function HistoryDetailPage() {
  return <HistoryDetail />;
}

export default HistoryDetailPage;

export async function loader({ params }) {
  const orderId = params.orderId;
  // sử dụng hàm bất đồng bộ để fetch data từ server
  const response = await fetch(`http://localhost:5001/order`);
  // nếu lỗi thì trả về messesge không thể lấy dữ liệu sản phẩm
  if (!response.ok) {
    throw json({ message: "Could not fetch order data" }, { status: 500 });
  }
  //còn không thì chuyển đổi qua json
  // và trả về kết quả
  const resData = await response.json();
  console.log(resData);
  return resData;
}
